html {
  box-sizing: border-box;
  overflow-x: hidden;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: 'Lato-Regular';
  margin: 0;
  padding: 0;
}
h1,
h2,
p,
ul,
li,
figure,
input,
button {
  list-style: none;
  margin: 0;
}
a {
  text-decoration: none;
}
input:active,
input:focus,
input:hover {
  outline: 0;
  outline-offset: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}
button:active,
button:focus,
button:hover {
  outline: 0;
  outline-offset: 0;
}
.header-top,
.header-main,
.support,
.categories,
.collection,
.brands,
.response,
.footer {
  max-width: 1290px;
  margin: 0 auto;
}
/* container */
.header-top,
.header-main,
.slider,
.response-items,
.footer {
  position: relative;
}
.header-top::after,
.header-main::after,
.slider::after,
.response-items:after,
.footer::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.icon {
  height: 10px;
  width: 10px;
  margin-right: 20px;
}
.social {
  height: 34px;
  width: 34px;
  fill: #d9bf8f;
  margin-right: 12px;
}
.social:hover {
  fill: #000;
}
.sup_icon {
  fill: #fff;
}
.s_air {
  height: 38px;
  width: 38px;
  margin-right: 33px;
}
.s_time {
  height: 42px;
  width: 41px;
  margin-right: 22px;
}
.s_help {
  height: 48px;
  width: 48px;
  margin-right: 28px;
}
.footer-icon,
.burger {
  fill: #000;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.copyright-icon {
  fill: #818181;
  height: 16px;
  width: 32px;
  flex-shrink: 0;
  margin-right: 20px;
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f2f2f2;
  padding: 14px 10px;
}
.header-top__phone {
  display: flex;
  align-items: center;
  font-style: normal;
}
.header-top__phone-link {
  font-size: 11px;
  color: #000;
}
.header-top__phone-link:hover {
  color: #d9bf8f;
  fill: #d9bf8f;
}
.header-top__bar {
  display: flex;
  font-size: 11px;
}
.bar-list {
  display: flex;
}
.bar-item:not(:last-child) {
  border-right: 1px solid #000;
}
.bar-item:first-child {
  padding-right: 22px;
}
.bar-item:nth-child(2) {
  padding-right: 35px;
}
.bar-item:nth-child(3) {
  padding-right: 33px;
}
.bar-item:first-child,
.bar-item:nth-child(2),
.bar-item:nth-child(3) {
  margin-right: 33px;
}
.bar-item__link {
  color: #000;
}
.bar-item__link:hover {
  color: #d9bf8f;
}
.burger-btn {
  display: none;
  padding: 10px 15px;
  margin-top: 15px;
}
.burger-btn:hover {
  cursor: pointer;
}
.burger-btn:hover .burger {
  fill: #d9bf8f;
}
/*  header-main  */
.header-main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.menu-list {
  display: flex;
  padding: 0;
}
.menu-item:first-child {
  margin-right: 27px;
}
.menu-item:nth-child(2) {
  margin-right: 15px;
}
.menu-item:nth-child(3) {
  margin-right: 26px;
}
.menu-item__link {
  font-family: 'Playfair_Display-Italic';
  font-size: 18.32px;
  line-height: 0.983;
  color: #000;
}
.menu-item__link:hover {
  color: #d9bf8f;
}
.header-main__logo {
  padding-top: 32px;
  padding-bottom: 25px;
}
.logo-title {
  font-family: 'KrinkesDecorPERSONAL';
  font-size: 72px;
  line-height: 1.15;
}
.header-main__social-media {
  display: flex;
  align-items: center;
}
.social-media__form {
  color: #d9bf8f;
  margin-left: 32px;
}
.form-currency {
  padding: 14px;
  background: #000;
  font-family: 'Lato-Bold';
  font-size: 12px;
  line-height: 1.5;
  color: #fff;
  border: 0;
}
.form-currency:hover {
  color: #d9bf8f;
  cursor: pointer;
}
.option {
  padding: 2px;
  color: inherit;
}
@media screen and (max-width: 900px) {
  .header-main,
  .header-main__social-media {
    flex-direction: column;
  }
  .header-main__menu {
    padding-top: 20px;
  }
  .social-media__form {
    margin-left: 0;
    padding-top: 15px;
    padding-bottom: 5px;
  }
}
@media screen and (max-width: 680px) {
  .header-top {
    flex-direction: column;
  }
  .header-top__phone-link {
    padding-bottom: 10px;
  }
  .bar-list {
    padding: 0;
  }
}
@media screen and (max-width: 500px) {
  .header-top {
    position: relative;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  .header-top .header-top__phone-link {
    padding-bottom: 0;
  }
  .header-top .header-top__bar {
    display: none;
  }
  .header-top .bar-list {
    position: absolute;
    flex-direction: column;
    justify-content: center;
    top: 53px;
    left: 50%;
    background: #f2f2f2;
    z-index: 555;
  }
  .header-top .bar-item {
    padding: 10px;
    border-right: none;
    margin-right: 0;
  }
  .header-top .burger-btn {
    display: block;
    padding: 0;
    margin-top: 0;
  }
  .header-main__menu {
    padding-top: 10px;
  }
  .header-main__logo {
    padding-top: 25px;
    padding-bottom: 10px;
  }
  .menu-item__link {
    font-size: 17px;
  }
  .social-media__form {
    padding-top: 10px;
  }
  .form-currency {
    padding: 10px;
  }
}
.slider-main {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  line-height: 0.8;
}
.slider-main::before {
  position: absolute;
  content: '';
  height: 0;
  width: 0;
  top: 0;
  left: calc(50% - 200px);
  border-top: 60px solid #fff;
  border-left: 200px solid transparent;
  border-right: 200px solid transparent;
  z-index: 222;
}
.slider-back {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 53;
}
.slider-item__content {
  position: absolute;
  font-family: 'Lato-Bold';
  top: 18%;
  left: calc(50% - 260px/2);
  z-index: 55;
}
.slider-item {
  display: flex;
  border: 2px solid #fff;
  padding: 6px;
}
.content-info {
  display: flex;
  flex-direction: column;
  min-width: 257px;
  border: 1px solid #fff;
  padding: 29px 0 33px 0;
}
.content-info__title {
  font-size: 19.44px;
  color: #fff;
}
.content-info__offer {
  padding-top: 38px;
  padding-bottom: 34px;
  font-size: 58.81px;
  color: #000;
}
.content-info__action {
  position: relative;
  font-size: 19.6px;
  color: #fff;
}
.content-info__action-decor {
  position: relative;
}
.content-info__action-decor::before,
.content-info__action-decor::after,
.content-info__action::before,
.content-info__action::after {
  content: '';
  position: absolute;
  height: 1px;
  background-color: #fff;
}
.content-info__action-decor::before,
.content-info__action::before {
  width: 33px;
}
.content-info__action-decor::before {
  top: 31%;
  left: 21.5%;
}
.content-info__action::before {
  top: 35%;
  right: -74%;
}
.content-info__action-decor::after,
.content-info__action::after {
  width: 60px;
}
.content-info__action-decor::after {
  left: 11%;
  bottom: 7%;
}
.content-info__action::after {
  left: 118%;
  bottom: 20%;
}
.content-info__sale {
  padding-top: 28px;
  line-height: 0.375;
  font-size: 32px;
  color: #000;
}
.slider-btn {
  position: absolute;
  left: calc(50% - 189px/2);
  margin-top: 20%;
  padding: 19px 34px 16px 25px;
  font-size: 24px;
  color: #fff;
  background: #f09d66;
}
.slider-btn:hover {
  color: #f09d66;
  background: #fff;
}
.slider-btn:active {
  border-radius: 0;
  box-shadow: 0 0 0 2px #f0975c;
}
@media screen and (max-width: 1200px) {
  .slider-main::before {
    left: calc(50% - 180px);
    border-top: 55px solid #fff;
    border-left: 180px solid transparent;
    border-right: 180px solid transparent;
  }
  .slider-item__content {
    top: 16%;
  }
  .content-info {
    min-width: 240px;
  }
}
@media screen and (max-width: 1140px) {
  .content-info {
    padding: 26px 0 30px 0;
  }
  .content-info__offer {
    padding-top: 35px;
    padding-bottom: 30px;
    font-size: 50px;
  }
  .content-info__sale {
    padding-top: 25px;
    font-size: 28px;
  }
  .slider-btn {
    position: absolute;
