  }
  .slider-btn {
    position: absolute;
    left: calc(50% - 120px/2);
    margin-top: 10%;
    padding: 12px 18px 12px 16px;
    font-size: 16px;
  }
}
@media screen and (max-width: 750px) {
  .slider-main::before {
    left: calc(50% - 130px);
    border-top: 38px solid #fff;
